@import url(https://fonts.googleapis.com/css2?family=Anonymous+Pro&display=swap);
body {
  margin: 0;
  background-color: #EAEAEA;
}
a{
  text-decoration: none;
  color: #1289AE;
}
.finish {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #000;
  position: relative;
  color: #fff;
}
.thanks-msg {
  font-family: cursive;
  letter-spacing: 10px;
  font-size: 35px;
}
.sparkle-1{
  width: 30px;
}
.sparkle-2{
  width: 20px;
  -webkit-transform: translate(900%, -100%);
          transform: translate(900%, -100%);
}
.main_frame {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.main_logo_div {
  top: -10vh;
  height: 40vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
  font-size: 1.5vw;
  text-align: center;
  justify-content: space-around;
  font-weight: 700;
  position: relative;
}
.main_logo {
  width: 170vw;
  -webkit-animation: logo 1s forwards linear;
          animation: logo 1s forwards linear;
  -webkit-animation-delay: .5s;
          animation-delay: .5s;
}
@-webkit-keyframes logo {
  0%{
      width: 170vw;
  }
  100%{
      width: 5vw;
  }
}
@keyframes logo {
  0%{
      width: 170vw;
  }
  100%{
      width: 5vw;
  }
}
.start_section {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.start_slider {
  -webkit-animation: slide 0.5s forwards linear;
          animation: slide 0.5s forwards linear;
  -webkit-animation-delay: 1.8s;
          animation-delay: 1.8s;
  position: relative;
  bottom: 10vh;
  font-weight: 500;
  font-family: monospace;
  font-size: 120%;
}
.start_slider:hover {
  text-shadow: 3px 0px #232323;
}
@-webkit-keyframes slide {
  from{
    bottom: 10vh;
  }
  to{
    bottom: 0;
  }
}
@keyframes slide {
  from{
    bottom: 10vh;
  }
  to{
    bottom: 0;
  }
}
.ready_go {
  height: 10vh;
  overflow: hidden;
}
.logo-styles {
  width: 45%;
  height: 70vh;
}
.start_p {
  position: absolute;
  font-weight: bold;
  font-size: 1.5em;
  line-height: 1.5em;
  text-align: center;
  color: #232323;
  cursor: pointer;
  bottom: 0;
  top: 100%;
  text-align: center;
}
.content-wrapper {
  display: flex;
  justify-content: space-between;
  width: 80vw;
  height: 70vh;
}
.logo-wrapper{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 80vw;
  padding-bottom: 1%;
  border-bottom: 1.8px solid #232323;
  margin-bottom: 1vh;
  font-family: 'Anonymous Pro';
  font-size: 2vw;
  font-weight: 700;
}
.redberry_label {
  height: 2.5vh;
}
.logo-styles {
  position: relative;
  height: 70vh;
}
.target-label {
  display: block;
  font-size: 90%;
  font-weight: 700;
  margin: 1.5vh 0;
}
.target {
  width: 22vw;
  position: relative;
  top: 3vh;
}
.fa {
  font-size: 200%;
}
.nav-button {
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.nav-wrapper {
  width: 12vw;
  display: flex;
  justify-content: space-around;
}
.text-input, .custom-input {
  background-color: transparent;
  height: 5vh;
  width: 25vw;
  border: 0.8px solid #232323;
  padding: .5% 4%;
  font-size: 80%;
  font-weight: 400;
  color: #232323 !important;

}
.error-span {
   display: block;
   margin: .5vh 0 4vh 2vh;
   font-size: 80%;
   color: #F15524;
   width: 30vw;
}
.radios{
  font-size: 80%;
  display: block;
  width: 27vw;
  margin: 2vh 0;
  overflow: auto;
}
.radios::-webkit-scrollbar {
	display: none;
}
input{
  margin: 0 2vh;
}
.text-input {
   margin: 0;
}
.required:after {
  content: '*';
}
.question {
  font-weight: 700;
  margin-top: 4vh;
}
.book {
   margin: 4vh 6vh;
}
.marginal_note {
  font-size: 70%;
  width: 15vw;
  color: #626262;
  margin-top: 5vh;
}
textarea {
	height: 130px;
	width: 100%;
	flex-shrink: 0;
  resize: none;
	display: block;
  box-sizing: border-box;
  font-family: inherit;
}
.submit {
  float: right;
  margin: 3vh 0;
  padding: 2vh 3vh;
  color: #fff;
  background-color: #208298;
  border: none;
  border-radius: 20px;
  font-weight: 700;
  cursor: pointer;
}
.scan {
  background-color: #D6D16E;
  width: 7vw;
  height: 3vh;
  position: absolute;
  left: 35vw;
  top: 30vh;
  -webkit-animation: scan .3s linear forwards;
          animation: scan .3s linear forwards;
}
.fever {
  background-color: #DD3939;
  position: absolute;
  width: 5vw;
  height: 5vw;
  left: 70vw;
  top: 30vh;
  border-radius: 50%;
  -webkit-animation: fever .3s linear forwards;
          animation: fever .3s linear forwards;
}
@-webkit-keyframes scan {
  0% {
    left: 35vw;
    top: 30vh;
    width: 7vw;
    height: 3vh;
  }100% {
    left: 46vw;
    top: 16vh;
    width: 27vw;
    height: 7vh;
  }
}
@keyframes scan {
  0% {
    left: 35vw;
    top: 30vh;
    width: 7vw;
    height: 3vh;
  }100% {
    left: 46vw;
    top: 16vh;
    width: 27vw;
    height: 7vh;
  }
}
@-webkit-keyframes fever {
  0% {
    left: 70vw;
    top: 30vh;
    width: 5vw;
    height: 5vw;
  }100%{
    left: 55vw;
    top: 35vh;
    width: 10vw;
    height: 10vw;
  }
}
@keyframes fever {
  0% {
    left: 70vw;
    top: 30vh;
    width: 5vw;
    height: 5vw;
  }100%{
    left: 55vw;
    top: 35vh;
    width: 10vw;
    height: 10vw;
  }
}
.star {
  width: 15vw;
  height: 18vw;
  position: relative;
  left: 15vw;
  top: 15vh;
  -webkit-animation: vaccine .3s linear forwards;
          animation: vaccine .3s linear forwards;
}
@-webkit-keyframes vaccine{
  0%{
    left: 10vw;
    top: 15vh;
  }100%{
    left: 16vw;
    top: -3vh;
  }
}
@keyframes vaccine{
  0%{
    left: 10vw;
    top: 15vh;
  }100%{
    left: 16vw;
    top: -3vh;
  }
}
.heart {
  width: 9vw;
  height: 9vw;
  position: relative;
  left: 15vw;
  top: 6vh;
  -webkit-animation: office .3s linear forwards;
          animation: office .3s linear forwards;
}
@-webkit-keyframes office {
  0%{
    left: 15vw;
    top: -1vh;
  }100%{
    left: 18vw;
    top: 6vh;
  }
}
@keyframes office {
  0%{
    left: 15vw;
    top: -1vh;
  }100%{
    left: 18vw;
    top: 6vh;
  }
}

